.hero {
  background: url(../../img/chaos-corner.png) no-repeat center/cover;
  display: flex;
  width: 100%;
  height: 450px;
}

form {
  display: none;
  /* display: flex; */
  justify-content: space-evenly;
  align-items: center;
  max-width: 700px;
  width: 100%;
  margin: auto;
  margin-bottom: 5%;
  padding: 0.6rem 1rem;
  /* Created with https://www.css-gradient.com */
  background: #4b4acc;
  background: -webkit-linear-gradient(top left, #4b4acc, #6b9eed);
  background: -moz-linear-gradient(top left, #4b4acc, #6b9eed);
  background: linear-gradient(to bottom right, #4b4acc, #6b9eed);
  box-shadow: rgba(0, 0, 0, 0.9) 0px 8px 24px;
  border-radius: 25px;
}

form label {
  font-size: 0.8rem;
  padding-bottom: 2px;
}

.name,
.email {
  display: flex;
  flex-direction: column;
}

form input {
  background-color: transparent;
  border: none;
  font-family: var(--ff-primary);
}

form input::placeholder {
  color: #333;
  font-style: italic;
}

form input:focus {
  outline: none;
}

form .input_name {
  width: 300px;
}

.email {
  border-left: 1px solid #333;
  padding-left: 6px;
  margin-right: 4em;
}

.search_btn {
  display: flex;
  align-items: center;
}

.search_btn button {
  display: none;
}

/* add subscribe section to footer */
@media screen and (max-width: 720px) {
  form {
    /* display: flex; */
    display: none;
    flex-direction: column;
    max-width: 100%;
    margin: auto 1rem;
  }

  .name,
  .email {
    width: 100%;
    padding: 0.2rem;
    border-left: none;
  }

  .input_name {
    font-size: 0.8rem;
  }

  .email {
    margin-right: 0;
  }

  .border {
    border-top: 1px solid #333;
    padding: 3rem;
  }

  form label {
    padding: 0.4rem 0;
  }

  .search_btn .btn {
    display: block;
    background-color: transparent;
    color: #fff;
    width: 100%;
    font-weight: 600;
    font-size: 1rem;
    border: none;
    padding: 2rem;
    margin: 0.5rem 0;
    cursor: pointer;
  }

  .search_btn .btn:hover {
    background-color: #4b4acc;
  }

  .icon {
    display: none;
  }
}
