.author {
  max-width: 1040px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  padding: 1rem;
}

.author img {
  height: 450px;
  max-width: 512px;
}

.author h2 {
  padding-top: 30%;
}

.author span {
  color: var(--clr-p);
}

.author p {
  font-size: 1rem;
  color: var(--txt-primary);
  padding: 1.5rem 0;
}

.author button {
  background: var(--bg-gradient-primary);
  background: var(--bg-gradient-secondary);

  color: #fff;
  font-weight: 600;
  font-size: 1rem;
  border: none;
  padding: 0.5rem 1.5rem;
  margin: 0.5rem 0;
  cursor: pointer;
  border-radius: 5px;
}

.author button:focus,
.author button:hover {
  background-color: #fff;
  transform: scale(1.1);
  /* color: var(--clr-p); */
  transition: all 0.3s ease-in-out;
  border: none;
  outline: none;
}

@media screen and (max-width: 920px) {
  .author {
    grid-template-columns: 1fr;
  }

  .author img {
    max-width: 80%;
  }

  .author .left {
    display: flex;
    justify-content: center;
  }

  .author h2 {
    padding-top: 2rem;
  }
}
