.footer {
  width: 100%;
  height: 80px;
  background-color: #444;
}

.container h3 {
  background-image: linear-gradient(to top, #ff0844 0%, #ffb199 100%);
  background-size: 100%;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
  padding: 1rem;
  margin-bottom: 0;
}

.container .header_subtitle {
  font-size: var(--fs-header);
  font-weight: 400;
  color: var(--clr-p);
}

.container {
  max-height: 1040px;
  height: 100%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  font-weight: 500;
}

.container .social_media {
  display: flex;
  justify-content: end;
  color: var(--clr-p);
}

.container button {
  background: var(--bg-gradient-primary);
  background: var(--bg-gradient-secondary);

  color: #fff;
  font-weight: 600;
  font-size: 1rem;
  border: none;
  padding: 0.5rem 1.5rem;
  margin: 0.5rem 0;
  cursor: pointer;
  border-radius: 5px;
}

.container button:focus,
.container button:hover {
  background-color: #fff;
  transform: scale(1.1);
  /* color: var(--clr-p); */
  transition: all 0.3s ease-in-out;
  border: none;
  outline: none;
}
