.find {
  max-width: 1040px;
  margin: auto;
}

.heading h1 {
  font-size: 3rem;
  text-align: center;
  padding: 2rem;
}

.heading p {
  text-align: center;
}

.text_bg {
  background-color: var(--clr-p);
  height: 2rem;
  max-width: 100%;
  margin: auto;
  display: flex;

  align-items: center;
  justify-content: center;
  padding: 1rem 2rem;
}

.slider_container {
  padding: 4rem 0;
}

.card {
  margin: 0.5rem;
  border-radius: 10px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

/* .card:hover,
.card:focus {
  transform: translateX(0.6rem);
  border-radius: 10px;
  padding-bottom: 1rem;
  margin: 1rem 0;
} */

.card p {
  color: var(--clr-p);
  font-size: 0.9rem;
  text-align: center;
  padding: 0.5rem 0.8rem;
}

.card img {
  width: 100%;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  height: 250px;
  object-fit: cover;
}
