.next {
  max-width: 1040px;
  margin: auto;
  padding: 5rem 1rem;
}

.container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 3rem;
  padding: 4rem 1rem;
}

.heading h2 {
  font-size: 3rem;
  text-align: center;
  padding: 2rem;
}

.heading p {
  text-align: center;
}

.text_bg {
  background: var(--clr-p);
  max-width: 650px;
  margin: auto;
}

.text_bg p {
  font-size: 1rem;
}

.card {
  width: 100%;
  border-radius: 10px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  text-align: center;
  padding: 1rem 0.5rem;
}

.card img {
  width: 100%;
  height: 250px;
  object-fit: cover;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
}

.card p {
  color: var(--clr-p);
  font-style: italic;
}

.card:focus,
.card:hover {
  transform: scale(1.1);
}

@media screen and (max-width: 768px) {
  .next .container {
    grid-template-columns: repeat(2, 1fr);
  }

  .heading h2 {
    font-size: 2rem;
  }

  .heading p {
    font-size: 0.8rem;
  }
}

@media screen and (max-width: 568px) {
  .next .container {
    grid-template-columns: 1fr;
  }
}
