.navbar {
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  font-weight: 500;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
  /* color: var(--clr-secondairy); */
}

.header_title {
  background-image: linear-gradient(to top, #ff0844 0%, #ffb199 100%);
  background-size: 100%;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
}

.header_subtitle {
  font-size: var(--fs-header);
  font-weight: 400;
  color: var(--clr-p);
}

.menu {
  display: flex;
  align-items: center;
}

.menu li {
  padding: 1rem;
}

.navbar a {
  font-size: 1rem;
  color: var(--clr-p);
}

.mobile_btn {
  display: none;
}

@media screen and (max-width: 720px) {
  .mobile_btn {
    display: block;
    position: absolute;
    right: 1rem;
    cursor: pointer;
  }

  .menu {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: fixed;
    left: -100%;
    right: 0;
    bottom: 0;
    top: 0;
    width: 100%;
    height: 100vh;

    background-color: #f8f8f8;
    /* z-index: 10; */
    transition: left 1s;
  }

  .active {
    left: 0;
  }

  .navar a {
    font-size: 2rem;
  }
}
